import React, {useEffect, useState} from "react";
import './app.css';
import RowGroups from "../row-groups";
import PostList from "../post-list";
import AddPanel from "../add-panel";
import NasaApiGov from '../../services/nasa-api-gov';

import SearchPanel from "../search-panel";
import Post from "../post";

const App = () => {

    const [listPosts, setListPosts] = useState([]);
    const [search,setSearch] = useState("");
    useEffect(() => {
        const nasaApiGov = new NasaApiGov();
        nasaApiGov
            .getSearch(search)
            .then(list => {
                console.log(list);
                setListPosts(list.slice(0,6));
            });
        return ()=>setListPosts([]);
    }, [search]);
    return (
        <div className="container">
            <SearchPanel setSearch={setSearch}/>
            {/*<RowGroups groups={value.getGroups()}/>*/}
            {/*<AddPanel/>*/}
            <PostList posts={listPosts}/>
        </div>
    )
}

export default App;